.div{
   position: absolute;
   left: 50%;
   top: 50%;
   transform: translate(-50%,-50%);
   background-color: transparent;
   padding: 10px 10px;
   font-size: 40px;
   font-family: 'Get Schwifty';
   color:black ;
   letter-spacing: 1px;
   text-shadow: 0 0 5px  #44fb08, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #000, 0 0 25px #ff00de;
   -webkit-text-fill-color: #000;
   -webkit-text-stroke-color: #3d1441;
   -webkit-text-stroke-width:0.2px;   
}
 
.img{
   border-radius: 10%;
   width: 300px;
}